/* hamburger styles start*/
.overlay {
  display: none; }

.hamburger-icon-container {
  width: 30px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }

.hamburger-icon-container {
  border: 1px dotted #011C40; }

.hamburger {
  visibility: hidden;
  width: 24px;
  height: 12px; }

.hamburger-icon {
  position: relative;
  width: 24px;
  height: 2px;
  background-color: #F9FBFD;
  cursor: pointer; }

.hamburger-icon::before {
  position: absolute;
  left: 0;
  top: -4px;
  margin-top: -2px;
  content: '';
  display: block;
  width: 24px;
  height: 2px;
  background-color: #F9FBFD; }

.hamburger-icon::after {
  position: absolute;
  left: 0;
  top: 4px;
  margin-top: 2px;
  content: '';
  width: 24px;
  height: 2px;
  background-color: #F9FBFD; }

.hamburger-icon-container:hover + .hamburger-icon:hover,
.hamburger-icon-container:hover + .hamburger-icon:hover::before,
.hamburger-icon-container:hover + .hamburger-icon:hover::after {
  background-color: #011C40; }

.hamburger-icon:hover,
.hamburger-icon:hover::before,
.hamburger-icon:hover::after {
  background-color: #011C40; }

.hamburger-icon:active,
.hamburger-icon:active::before,
.hamburger-icon:active::after {
  background-color: #011C40; }

/* hamburger styles end*/
/* popap styles start*/
